import React from 'react'

import Data from './Data'
import Table from './Table'
import './App.css'



class Main extends React.Component {

	render() {
	return (
			<div className='container'>
					<Data {...this.props}/>
					<Table {...this.props}/>
			</div>
			);
	}
}

export default Main



